* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

ul {
  list-style: none; }

a,
a:hover {
  text-decoration: none; }

input,
button,
textarea,
select {
  outline: none !important; }

button {
  cursor: pointer; }

body {
  background: #fff;
  font-family: 'Exo 2', sans-serif;
  font-weight: 400; }

.fixed-top {
  position: fixed !important;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 99999;
  background: #fff !important; }

.menu-open {
  display: flex !important; }

.logo {
  z-index: 5000;
  width: 160px;
  height: 40px; }
  @media (max-width: 575.98px) {
    .logo {
      width: 130px;
      height: 32px; } }
  .logo img {
    width: 100%; }

.line {
  height: 2px;
  background: #f2f2f2;
  margin: 30px 0; }

.react {
  width: 60px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.like-btn {
  display: inline-flex;
  color: #333;
  margin-top: 15px; }

.is-active {
  color: #57d027;
  animation-name: animate;
  animation-duration: .8s;
  animation-iteration-count: 1;
  animation-timing-function: steps(28);
  animation-fill-mode: forwards; }

@keyframes animate {
  0% {
    background-position: left; }
  50% {
    background-position: right; }
  100% {
    background-position: right; } }

.header-nav .container {
  padding: 29px 15px;
  display: flex;
  justify-content: space-between;
  align-items: center; }

.header-menu {
  display: flex;
  justify-content: center;
  align-items: center; }
  @media (max-width: 1199.98px) {
    .header-menu {
      padding-right: 7px; } }
  @media (max-width: 991.98px) {
    .header-menu {
      padding: 0; } }
  @media (max-width: 991.98px) {
    .header-menu {
      width: 100%;
      position: fixed;
      height: 100%;
      background: #fff;
      left: 0;
      top: 0;
      z-index: 3333;
      flex-direction: column;
      justify-content: center;
      display: none; } }
  .header-menu__item:not(:last-child) {
    margin-right: 50px; }
    @media (max-width: 991.98px) {
      .header-menu__item:not(:last-child) {
        margin: 10px 0; } }
  .header-menu__link {
    text-transform: uppercase;
    font-size: 12px;
    padding: 3px 0;
    color: #333; }
    @media (max-width: 991.98px) {
      .header-menu__link {
        font-size: 22px;
        display: inline-block; } }
    @media (max-width: 575.98px) {
      .header-menu__link {
        font-size: 15px; } }
    .header-menu__link_active {
      border-top: 1px solid #333;
      border-bottom: 1px solid #333; }
  .header-menu__burger {
    cursor: pointer;
    position: relative;
    z-index: 4444;
    width: 30px;
    height: 12px;
    transform: rotate(0);
    transition: 0.3s;
    border: none;
    background: none;
    display: none; }
    @media (max-width: 991.98px) {
      .header-menu__burger {
        display: block; } }
    .header-menu__burger span {
      width: 100%;
      height: 3px;
      background: #333;
      border-radius: 10px;
      position: absolute;
      left: 0;
      transform: rotate(0);
      transition: 0.3s; }
      .header-menu__burger span:nth-child(1) {
        top: 0; }
      .header-menu__burger span:nth-child(2), .header-menu__burger span:nth-child(3) {
        top: 6px; }
      .header-menu__burger span:nth-child(4) {
        top: 12px; }
    .header-menu__burger.open span:nth-child(1), .header-menu__burger.open span:nth-child(4) {
      width: 0;
      left: 50%;
      top: 6px; }
    .header-menu__burger.open span:nth-child(2) {
      transform: rotate(45deg); }
    .header-menu__burger.open span:nth-child(3) {
      transform: rotate(-45deg); }

.gallery {
  padding: 60px 0 120px; }
  @media (max-width: 767.98px) {
    .gallery {
      padding: 30px 0 120px; } }
  @media (max-width: 575.98px) {
    .gallery {
      padding: 30px 0 60px; } }
  .gallery__title {
    text-transform: uppercase;
    font-size: 45px;
    line-height: 38px;
    color: #000; }
    @media (max-width: 575.98px) {
      .gallery__title {
        margin: 0;
        text-align: center; } }
  .gallery-slider__item {
    position: relative;
    overflow: hidden;
    border-top-left-radius: 6px;
    border-top-right-radius: 6px;
    margin-bottom: 30px;
    width: 23%;
    height: 220px; }
    @media (max-width: 991.98px) {
      .gallery-slider__item {
        width: 30%;
        height: 200px;
        margin-bottom: 20px; } }
    @media (max-width: 767.98px) {
      .gallery-slider__item {
        width: 45%;
        height: 200px;
        margin-bottom: 15px; } }
    @media (max-width: 575.98px) {
      .gallery-slider__item {
        width: 80%;
        height: 200px;
        margin-bottom: 15px; } }
    .gallery-slider__item:hover .gallery-slider__about {
      height: 120px;
      bottom: 0; }
  .gallery-slider__wrap {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    position: relative; }
    @media (max-width: 1199.98px) {
      .gallery-slider__wrap {
        justify-content: space-evenly; } }
  .gallery-slider__img {
    display: block;
    width: 100%;
    height: 100%; }
  .gallery-slider__more {
    display: none;
    width: 100%; }
    @media (max-width: 991.98px) {
      .gallery-slider__more {
        display: flex; } }
    .gallery-slider__more a {
      display: inline-block;
      margin-left: auto;
      font-size: 14px;
      color: #333;
      transition: 0.5s; }
      .gallery-slider__more a:hover {
        text-decoration: underline; }
  .gallery-slider__about {
    position: absolute;
    bottom: -20px;
    width: 100%;
    height: 0;
    background: #fbfbfb;
    padding: 20px 20px 0;
    transition: 0.5s; }
  .gallery-slider__text {
    font-size: 14px;
    line-height: 18px;
    color: #333; }
  .gallery-slider__num {
    margin-top: 30px;
    width: 100%;
    text-align: center; }
    @media (max-width: 575.98px) {
      .gallery-slider__num {
        width: 50%; } }
    .gallery-slider__num span {
      font-size: 24px;
      line-height: 24px;
      text-align: center;
      color: #e0e0e0; }
      .gallery-slider__num span:first-child {
        color: #333;
        margin-right: 15px; }
        @media (max-width: 575.98px) {
          .gallery-slider__num span:first-child {
            text-align: center;
            padding: 0; } }
      .gallery-slider__num span:last-child {
        color: #bdbdbd;
        margin-left: 15px; }
  .gallery-slider__prev, .gallery-slider__next {
    background: none;
    width: 53px;
    height: 53px;
    border: 1px solid #f2f2f2;
    border-radius: 50%;
    position: absolute;
    bottom: -15px;
    left: 38%;
    transform: translateX(-50%);
    z-index: 999;
    cursor: pointer;
    transition: 0.6s; }
    @media (max-width: 991.98px) {
      .gallery-slider__prev, .gallery-slider__next {
        left: 30%; } }
    @media (max-width: 767.98px) {
      .gallery-slider__prev, .gallery-slider__next {
        left: 20%; } }
    @media (max-width: 575.98px) {
      .gallery-slider__prev, .gallery-slider__next {
        display: none !important; } }
    .gallery-slider__prev:hover, .gallery-slider__next:hover {
      background: #f9f9f9; }
  .gallery-slider__next {
    left: 62%; }
    @media (max-width: 991.98px) {
      .gallery-slider__next {
        left: 70%; } }
    @media (max-width: 767.98px) {
      .gallery-slider__next {
        left: 80%; } }

